y:flex;z-index:-1}.errorpage .error-title{font-size:50px;text-align:center;font-weight:900}.errorpage article{font-size:20px;text-align:center;line-height:50px}.errorpage .error-back-button{font-weight:bold;padding:20px}.errorpage video{position:fixed;right:0;bottom:0;min-width:100%;min-height:100%;width:auto;height:auto;z-index:-5;opacity:.4;object-fit:cover}footer{width:100%;z-index:1;position:fixed;right:0;bottom:0;display:flex;padding:3% 4%;justify-content:flex-end}footer a{margin-left:20px}
